//variadveis do grid 
$cols: 12; //total de colunas
$gutter: 20px; // margin left, right
$l-device: 1200px; 
$m-device: 750px;
$s-device: 480;

//Box-sizing
*, *::after, *::before{
    box-sizing: border-box;
}

//Mixing
@mixin d($size) {
    @if $size == l{
        @media screen and (min-width: #{$l-device}){
            @content;
        }
    }
    @else if $size == m{
        @media screen and (min-width: #{$m-device}) and (max-width: #{$l-device} - 1) {
            @content;
        }
    }
    @else if $size == s{
        @media screen and (max-width: #{$m-device} - 1) {
            @content;
        }
    }
}

//func√£o do grid 
@function grid($col, $offset: false){
    $width: $col / $cols * 100 + %;
    $result: calc(#{$width} - #{$gutter});
    @if $offset == offset {
        $gutter: $gutter/2;
        $result: calc(#{$width} - #{$gutter});
    }
    @return $result;
}   

//Gerar as classes de grid 

@for $i from 1 through $cols{
    .grid-#{$i}{
        width:  grid($i);
    }
}

@mixin grid-structure {
    float: left;
    min-height: 1px;
    margin-left: $gutter /2;
    margin-right: $gutter /2;
    @include d(s){
        width: 100%;
        float: none;
        margin-left: 0px;
        margin-right: 0px;
    }
}

[class^="grid"]{
    @include grid-structure;
}

//gera as classes de offset
@for $i from 1 through $cols{
    .offset-#{$i} {
        margin-left: grid($i, offset);
        @extend %offset;
    }
    
}

@include d(s){
    %offset{
        margin-left: 0px;
    }
}

@mixin clearfix {
    &::after, &::before{
    content: '';
    display: table;
    clear: both;
    }
}

@mixin container {
    max-width: $l-device;
    margin: 0px auto;
    position: relative;
    @include d(m){
        max-width: $m-device;
        margin-left: 10px;
        margin-right: 10px;
    }
    @include clearfix;
}

.container{
    @include container;
}